.profileInfo {
}

.profileInfo img {
    height: 200px;
    width: auto;
    margin-bottom: 15px;
    border-radius: 50px;
    border: 5px solid #eda5ff;
}

.descriptionBlock {
    width: 250px;
    padding: 15px;
    font-size: 18px;
}

.contact {
    padding-left: 10px;
}

.chooseFileInput {
    display: none;
}

.chooseFileButton {
    height: 30px;
    border-radius: 10px;
    font-weight: bold;
    font-size: 15px;
    text-shadow: 0 0 10px rgb(254, 250, 255);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.38);
    border: none;
    background-color: rgb(243, 210, 255);
    color: #000000;
    margin-bottom: 15px;
    padding: 0 10px;
}

.chooseFileButton:hover {
    border-radius: 9px;
    height: 32px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.55);
}

.editOrSaveInfo {
    height: 30px;
    border-radius: 10px;
    font-weight: bold;
    font-size: 15px;
    text-shadow: 0 0 10px rgb(254, 250, 255);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.38);
    border: none;
    background-color: rgb(243, 210, 255);
    color: #000000;
    margin: 11px 0;
    padding: 0 10px;
}

.editOrSaveInfo:hover {
    border-radius: 9px;
    height: 32px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.55);
}

.description {
    margin: 3px 5px;
}

.fullName {
    margin: 0 0 10px 0;
    border-radius: 5px;
    background-color: #faedff;
    font-size: 20px;
    height: 30px;
    width: 240px;
    box-shadow: 1px 1px 10px rgba(145, 28, 182, 0.51);
    text-align: start;
    display: flex;
    align-items: center;
    padding: 5px 10px;
}

