.posts {
    margin-top: 10px;
}

.postsBlock {
    padding: 10px;
}

.blockName {
    margin: 15px 3px;
    border-radius: 5px;
    background-color: #faedff;
    font-size: 20px;
    height: 30px;
    width: 240px;
    box-shadow: 1px 1px 10px rgba(145, 28, 182, 0.51);
    text-align: start;
    display: flex;
    align-items: center;
    padding: 5px 10px;
}

.addPost {
    height: 30px;
    border-radius: 10px;
    font-weight: bold;
    font-size: 15px;
    text-shadow: 0 0 10px rgb(254, 250, 255);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.38);
    border: none;
    background-color: rgb(243, 210, 255);
    color: #000000;
    margin: 10px 0 20px 0;
    padding: 0 10px;
}

.addPost:hover {
    border-radius: 9px;
    height: 32px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.55);
}